const utils = (function() {
  return {
    init: init,
    initStorage: initStorage,
    formatDate: formatDate,
    httpPost: httpPost,
    isArray: isArray,
    exportExcel: exportExcel,
    isIdNum: isIdNum,
    isEmail: isEmail,
    isMobile: isMobile,
    toDouble: toDouble,
    getBirthdayById: getBirthdayById,
    getGenderById: getGenderById,
    birthDayAge: birthDayAge,
    filterNumber: filterNumber,
    copyObject: copyObject,
    cleanJSON: cleanJSON,
    linkTo: linkTo,
    storage: storage,
    removeStorage: removeStorage,
    scrollUnique: scrollUnique
  }

  let $server = '';
  let $http = null;
  let $root = null;
  let $connect = null;

  function init(root) {
    $root = root;
    $server = root.$config.server;
    $http = root.$http;
    $connect = $http.create({
      //timeout: 5000
    });
  }

  function initStorage() {
    var storage = $root.$utils.storage('loginInfo')
    $root.$root.code = storage.code
    $root.$root.department = storage.department
    $root.$root.gp_account = storage.gp_account
    $root.$root.image = storage.image
    $root.$root.introducer = storage.introducer
    $root.$root.mobile = storage.mobile
    $root.$root.name = storage.name
    $root.$root.oId = storage.oId
    $root.$root.oName = storage.oName
    $root.$root.roleCode = storage.roleCode
    $root.$root.phone = storage.phone
    $root.$root.roleName = storage.roleName
    $root.$root.sex = storage.sex
    $root.$root.sid = storage.sid
    $root.$root.sq580Acc = storage.sq580Acc
    $root.$root.teamId = storage.teamId
    $root.$root.teamName = storage.teamName
    $root.$root.token = storage.token
    $root.$root.typeId = storage.typeId
    $root.$root.userId = storage.userId
  }

  function formatDate(date, format){
    if (!date) return '-'
  	var date = new Date(date);
  	var paddNum = function (num) {
  		num += "";
  		return num.replace(/^(\d)$/, "0$1");
  	}
  	//指定格式字符
  	var cfg = {
  		yyyy: date.getFullYear(), //年 : 4位
  		yy: date.getFullYear().toString().substring(2), //年 : 2位
  		M: date.getMonth() + 1, //月 : 如果1位的时候不补0
  		MM: paddNum(date.getMonth() + 1), //月 : 如果1位的时候补0
  		d: date.getDate(), //日 : 如果1位的时候不补0=
  		dd: paddNum(date.getDate()), //日 : 如果1位的时候补0
  		hh: paddNum(date.getHours()), //时
  		mm: paddNum(date.getMinutes()), //分
  		ss: paddNum(date.getSeconds()) //秒
  	}
  	format || (format = "yyyy-MM-dd hh:mm:ss");
  	return format.replace(/([a-z])(\1)*/ig, function (m) {
  		return cfg[m];
  	})
  }

    /**
     * 同时有 Parameters 和 Payload
     * @param url
     * @param data
     * @param cb
     * @param errCb
     * @param complete
     */
    function httpPost(url, data, cb, errCb, complete) {
      if (url.indexOf('http') == -1 && url.indexOf('https') == -1) {
        url = $server+url
      }
      if (!$root.$root.token) $root.$router.replace('login')
      var option = {}

      //Parameters
      if (data && data.params) {
        option.params = data.params
      }
      //headers
      option.headers = {}
      if (data && data.headers) {
        option.headers = data.headers
      }
      //通用 token
      // option.headers.token = $root.token

      var payload = {}
      if (data && data.payload) {
        payload = data.payload
      }
      $connect.post(url, payload, option).then((res) => {
        var result = res.data
        if (result.err == 0) {
          cb && cb(result)
        } else if (result.err == 4){
          return $root.$router.push('/login')
        } else if (!result) {
          errCb && errCb({errmsg: '网络连接失败', msg: '网络连接失败'})
        } else {
          errCb && errCb(result)
        }

        complete && complete()
      }).catch((e) => {
        errCb && errCb({errmsg: '网络连接失败', msg: '网络连接失败'})
      })
    }

  /**
   * 判断是否为数组
   */
  function isArray(arr) {
    return Object.prototype.toString.call(arr)=='[object Array]'
  }

  //导出 Excel
  function exportExcel(url, param) {
    if (url.indexOf('.com') == -1) {
      url = $server+url
    }
    url  = url+'?'
    for (var key in param) {
      if (param[key] !== '') {
				var oValue = param[key]
				if (typeof(oValue) != 'string') {
					oValue = JSON.stringify(oValue)
				}
        url = url+key+'='+oValue+'&';
      }
    }
    // window.open(url)
    var saveLink = document.createElement( 'a')
    saveLink.href = url
    saveLink.target = '_blank'
    // saveLink.download = fileName
  	saveLink.click()
  }

  // 判断是不是身份证号
  function isIdNum(num) {
    num = num.toString().toUpperCase();
    if (!(/(^\d{15}$)|(^\d{17}([0-9]|X)$)/.test(num))) return false
    var len = num.length, re;
    if (len == 15) {
      re = new RegExp(/^(\d{6})(\d{2})(\d{2})(\d{2})(\d{3})$/)
      var arrSplit = num.match(re)
      var dtmBirth = new Date('19' + arrSplit[2] + '/' + arrSplit[3] + '/' + arrSplit[4])
      if (!(dtmBirth.getYear() == Number(arrSplit[2])
        && ((dtmBirth.getMonth() + 1) == Number(arrSplit[3]))
        && dtmBirth.getDate() == Number(arrSplit[4]))) {
        return false
      } else {
        var arrInt = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2)
        var arrCh = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2')
        var nTemp = 0, i;
        num = num.substr(0, 6) + '19' + num.substr(6, num.length - 6);
        for(i = 0; i < 17; i ++) {
          nTemp += num.substr(i, 1) * arrInt[i]
        }
        num += arrCh[nTemp % 11]
        return true
      }
    }

    if (len == 18) {
      re = new RegExp(/^(\d{6})(\d{4})(\d{2})(\d{2})(\d{3})([0-9]|X)$/);
      var arrSplit = num.match(re);
      var dtmBirth = new Date(arrSplit[2] + "/" + arrSplit[3] + "/" + arrSplit[4]);
      if (!(dtmBirth.getFullYear() == Number(arrSplit[2])
        && (dtmBirth.getMonth() + 1) == Number(arrSplit[3])
        && dtmBirth.getDate() == Number(arrSplit[4]))) {
        return false
      } else {
        var valnum;
        var arrInt = new Array(7, 9, 10, 5, 8, 4, 2, 1, 6, 3, 7, 9, 10, 5, 8, 4, 2);
        var arrCh = new Array('1', '0', 'X', '9', '8', '7', '6', '5', '4', '3', '2');
        var nTemp = 0, i;
        for(i = 0; i < 17; i ++) {
          nTemp += num.substr(i, 1) * arrInt[i]
        }
        valnum = arrCh[nTemp % 11]
        if (valnum != num.substr(17, 1)) {
          return false
        }
        return true
      }
    }
    return false
  }

  // 判断是不是邮箱
  function isEmail(str) {
    return /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/.test(str)
  }

  // 判断是不是手机号
  function isMobile(n) {
    return (/^1(3|4|5|7|8)\d{9}$/).test(n)
  }

  function toDouble(n) {
    return n.toString()[1] ? n : '0'+n
  }

  // 通过身份证获取生日
  function getBirthdayById(id, split) {
  	if (!this.isIdNum(id)) return
    var tmpStr = ''
    var split = split || '-'
    id = id.toString()
    if (id.length == 15) {
      tmpStr = "19" + id.substring(6, 12)
      tmpStr = tmpStr.substring(0, 4) + split + tmpStr.substring(4, 6) + split + tmpStr.substring(6)
    } else {
      tmpStr = id.substring(6, 14)
      tmpStr = tmpStr.substring(0, 4) + split + tmpStr.substring(4, 6) + split + tmpStr.substring(6)
    }
    return tmpStr
  }

  // 通过身份证号获取性别, 男1， 女2
  function getGenderById(id) {
  	if (!this.isIdNum(id)) return ''
    id = id.toString()
    if (id.length === 18) {
      return id.substr(16, 1) % 2 == 1 ? 1 : 2
    } else if (id.length === 15) {
      return id.substr(14, 1) % 2 == 1 ? 1 : 2
    }
  }

  //通过生日获取年龄
  function birthDayAge(birth) {
  	if (!birth) return ''

  	if (birth) {
  		var dis = new Date().getFullYear() - new Date(birth).getFullYear()
  		var disM = new Date().getMonth() - new Date(birth).getMonth()
  		var disD = new Date().getDate() - new Date(birth).getDate()
  		if (dis < 0) return ''

  		if (dis == 0) {
        if (disM == 0) {
          return disD >= 0 ? 0 : ''
  			} else {
          return disM > 0 ? dis : ''
        }
  		} else if (dis > 0) {
        if (disM == 0) {
  				return disD >= 0 ? dis : dis - 1
  			} else {
          return disM > 0 ? dis : dis - 1
  			}
  		}
  	}
  }

  //过滤字符串,只能是数字
  function filterNumber(num) {
  	return num.toString().replace(/\D/ig, '')
  }

  function copyObject(obj) {
    var result = {}
    for (var i in obj) {
      result[i] = (typeof obj[i]) == 'object' ? copyObject(obj[i]) : obj[i]
    }
    return result
  }

  // json序列化
  function cleanJSON(json) {
    return JSON.parse(JSON.stringify(json))
  }

  // a 连接跳转
  function linkTo(url) {
    var a = document.createElement('a')
    a.href = url
    a.target = '_blank'
    a.click()
  }

  function storage(key, data) {
    var obj = JSON.parse(localStorage.getItem('util_storage')) || {};
    if (data || ['', 0, false].indexOf(data) > -1) {
      obj[key] = data;
      localStorage.setItem('util_storage', JSON.stringify(obj));
    } else {
      if (key) {
        return obj[key] || '';
      } else {
        return obj;
      }
    }
  }
  function removeStorage(key) {
    var obj = JSON.parse(localStorage.getItem('util_storage')) || {};
    if (key) {
      delete obj[key];
      localStorage.setItem('util_storage', JSON.stringify(obj));
    } else {
      localStorage.removeItem('util_storage');
    }
  }

  // 单个元素滚动
  function scrollUnique(ele) {
		var type = document.mozHidden ? 'DOMMouseScroll' : 'mousewheel'
		ele.addEventListener(type, function(e) {
			var scrollTop = this.scrollTop,
					scrollHeight = this.scrollHeight,
					height = this.clientHeight,
					e = e || event;
			var delta = e.wheelDelta ? e.wheelDelta : -(e.detail || 0)

			if ((delta > 0 && scrollTop <= delta) || (delta < 0 && scrollHeight - height - scrollTop <= -1 * delta)) {
				this.scrollTop = delta > 0 ? 0 : scrollHeight
				e.preventDefault()
			}
		})
	}
})()

export default {
  install(Vue){
    Vue.prototype.$utils = utils
  }
}
