<template>
  <el-menu class="el-menu-vertical-demo" text-color="#fff"
  	background-color="#324157" :router="true" :unique-opened="true">
    <template v-for="(item, index) in menus">
      <el-submenu v-if="item.subMenus" :index="index+''">
        <template slot="title">
          <i :class="item.icon"></i>{{item.title}}
        </template>
        <el-menu-item :index="el.path" :class="{'color-0ae' : $route.path == el.path}"
          v-for="(el, key) in item.subMenus">{{el.title}}</el-menu-item>
      </el-submenu>

      <el-menu-item v-else :class="{'color-0ae' : $route.path == item.path}" :index="item.path">
        <i :class="item.icon"></i>{{item.title}}
      </el-menu-item>
    </template>
  </el-menu>
</template>

<script type="text/babel">
	export default {
    name: "Menus",
    props: {
      menus: ''
    }
  }
</script>
