<template>
  <header class="vc-page-header fixed clearfix">
    <a>
      <h1>
        <img src="./assets/logo.png" alt title />
      </h1>
    </a>

    <ul>
      <li v-for="(item, index) in list" :key="index" 
				@mouseenter="mouseenter(index)" @mouseleave="mouseleave(index)">
        <router-link :to="{ path: item.key }" :class="{'active': item.active}">{{item.value}}</router-link>
      </li>
      <li class="tab-bar absolute width-100" ref="bar"></li>
    </ul>

    <el-dropdown trigger="click" @command="handleCommand">
      <el-button type="primary">
        更多菜单
        <i class="el-icon-arrow-down el-icon--right"></i>
      </el-button>
      <el-dropdown-menu slot="dropdown" class="width-120 align-center">
        <el-dropdown-item command="1">登录</el-dropdown-item>
      </el-dropdown-menu>
    </el-dropdown>

    <el-dialog title="登 录" :visible.sync="centerDialogVisible" width="30%" center>
      <el-input v-model="input" type="password" :maxlength="20" placeholder="随意输入任何内容"></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="centerDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirm">确 定</el-button>
      </span>
    </el-dialog>
  </header>
</template>

<script type="text/babel">
import md5 from "md5";
export default {
  name: "PageHeader",
  data() {
    return {
      list: [
        { key: "/basic", value: "js基础", active: false },
        { key: "/application", value: "vue组件", active: false },
        { key: "/demo", value: "案例", active: false }
      ],
      transform: "",
      centerDialogVisible: false,
      input: ""
    };
  },

  mounted() {
    this.list.forEach((val, index) => {
      if (val.key == "/demo") {
        val.active =
          this.$route.path.indexOf(val.key) === 0 ||
          this.$route.path.indexOf("/fullpage") === 0
            ? true
            : false;
      } else {
        val.active = this.$route.path.indexOf(val.key) === 0 ? true : false;
      }
      if (val.active) {
        this.$refs.bar.style.transform = `translateX(${index * 100}px)`;
      }
    });
  },

  methods: {
    mouseenter(index) {
      this.transform = this.$refs.bar.style.transform;
      this.$refs.bar.style.transform = `translateX(${index * 100}px)`;
    },
    mouseleave(index) {
      if (!this.list[index].active) {
        this.$refs.bar.style.transform = this.transform;
      }
    },
    handleCommand() {
      this.centerDialogVisible = true;
    },
    confirm() {
      if (md5(this.input) == "668aa5f00401ef6c71d808b31a3efc35") {
        this.$root.token = this.input;
        localStorage.setItem("pwd", this.input);
      }
      this.$message({
        showClose: true,
        message: "恭喜你, 登录成功!",
        type: "success"
      });
      this.input = "";
      this.centerDialogVisible = false;
    }
  },

  watch: {
    "$route.path"() {
      window.scrollTo(0, 0);
      this.list.forEach((val, index) => {
        if (val.key == "/demo") {
          val.active =
            this.$route.path.indexOf(val.key) === 0 ||
            this.$route.path.indexOf("/fullpage") === 0 ? true : false;
        } else {
          val.active = this.$route.path.indexOf(val.key) === 0 ? true : false;
        }
        if (val.active) {
          this.$refs.bar.style.transform = `translateX(${index * 100}px)`;
        }
      });
    }
  }
};
</script>
