import Vue from 'vue'
import Router from 'vue-router'
import home from '../views/basicJs/index.vue'
import fullpage from '../views/fullpage/index.vue'
Vue.use(Router)

const routes = [
	{
		path: '/', component: () => import('../views/index/index.vue'),
		children: [
			{ path: '/', redirect: '/basic' },
			{ path: '/basic', component: home,
				children: [
					{ path: '/', component: () => import('../views/index/readMe.vue') },
					{ path: 'string', component: () => import('../views/basicJs/string/index.vue') },
					{ path: 'closure', component: () => import('../views/basicJs/closure/index.vue') },
					{ path: 'width-height', component: () => import('../views/basicJs/width-height/index.vue') },
					{ path: 'transition-animation', component: () => import('../views/basicJs/transition-animation/index.vue') },
					{ path: 'meta', component: () => import('../views/basicJs/meta/index.vue') },
					{ path: 'link', component: () => import('../views/basicJs/link/index.vue') }
				]
			},

			{ path: '/application', component: home,
				children: [
					{ path: '/', redirect: '/application/device' },
					{ path: 'device', component: () => import('../views/application/device/index.vue') }
				]
			},

			{ path: '/demo', component: home,
				children: [
					{ path: '/', component: () => import('../views/demo/echarts/index.vue') },
					{ path: 'basic-table', component: () => import('../views/demo/basic-table/index.vue') },
					{ path: 'drag', component: () => import('../views/demo/drag/index.vue') },
					{ path: 'flash', component: () => import('../views/demo/flash/index.vue') },
					{ path: '3D-cube-animation', component: () => import('../views/demo/3D-cube-animation/index.vue') }
				]
			},

			{ path: '/fullpage', component: fullpage,
				children: [
					{ path: '/', component: () => import('../views/fullpage/fullpage-test/index.vue') },
				]
			}
		]
	}
]

routes.push({path: '*', redirect: '/basic' })

const router = new Router({
	routes
})

export default router
